@font-face {
  font-family: 'GothamRnd-Medium';
  src: url('fonts/gothamrounded-medium-webfont.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'GothamRnd-Book';
  src: url('fonts/gothamrounded-book-webfont.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'GothamRnd-Bold';
  src: url('fonts/gothamrounded-bold-webfont.woff') format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'HelveticaNeue';
  src: url('fonts/HelveticaNeueCyr-Roman.woff') format('woff');
  font-weight: bold;
}

/* ============ FONT COLORS BEGIN ============== */
html {
  --par-font-color: #444444;
  --tag-font-color: #F7F7F7;
  --light-gray-font-color: #938F8F; /* not selected metatags, menu options etc. */
  --dark-gray-font-color: #555555; /* selected metatags, link hovers, column title etc. */
  --card-title-font-color: #4E4E4E; /* quiz card title */
}
/* ============ FONT COLORS END ============== */

.everything {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 200%;
  background: #fcfaed;
}

.first-slide, .processing {
  top: 0;
  height: 50%;
  width: 100%;
  position: absolute;
  z-index:1;
}

.processing {
  background: #ddd4c1;
}

.second-slide {
  min-height: 50%;
  top: 50%;
  width: 100%;
  z-index: 0;
  position: absolute;
}

.App-logo, .logo {
  text-align: center;
  font-family: 'GothamRnd-Bold';
  text-transform: uppercase;
  font-size: 20px;
  color: #FFFFFF;
  letter-spacing: 0.18px;
  margin: auto;
}

.logo {
  color: var(--dark-gray-font-color);
}

.sticky-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  height: 58px;
  background: #ddd4c1;
}

.aligner {
  margin-top: 58px;
  position: absolute;
  width: 100%;
  height: calc(100% - 58px);
}

.aligned {
  margin: auto;
}

.par {
  text-align: justify;
  float: left;
  font-family: 'HelveticaNeue';
  font-size: 24px;
  color: var(--dark-gray-font-color);
  letter-spacing: 0;
  line-height: 30px;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.central-par {
  padding-top: 8%;
  max-width: 100%;
}

.top-par {
  padding-top: 24%;
}

.down-arrow {
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-left: 50%;
  padding-top: 16%;
  width: 8%;
}



